.telegram__link img {
  display: block;
  flex-shrink: 0;
  border-radius: 100%;
  background-color: #fff;
}

.seo {
  padding: 12rem 0;
}
@media (max-width: 1024px) {
  .seo {
    padding: 5rem 0;
  }
}
.seo__desc {
  margin: 0;
  margin-bottom: 1.2rem;
  font-size: 1.8rem;
  color: rgb(6, 8, 23);
  line-height: 1.333;
  position: relative;
}
@media (max-width: 768px) {
  .seo__desc {
    font-size: 1.7rem;
  }
}
.seo__desc::before {
  content: "";
  position: absolute;
  z-index: 3;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, #fff, transparent);
}
.seo__desc .more {
  display: none;
}
.seo__desc.active::before {
  display: none;
}
.seo__link {
  display: inline-block;
  color: transparent;
  font-size: 1.8rem;
  position: relative;
  line-height: 1.333;
  background: linear-gradient(to right, #d83ce4, #7919f5);
  -webkit-background-clip: text;
  background-clip: text;
  cursor: pointer;
}
.seo__link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(to right, #d83ce4, #7919f5);
}

.footer {
  padding: 5rem 0;
  background-color: #100e28;
}
.footer__col {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}
@media (max-width: 768px) {
  .footer__col {
    align-items: center;
    text-align: center;
  }
}
.footer__logo {
  display: inline-block;
  margin-bottom: 2rem;
}
.footer__desc {
  margin: 0;
  max-width: 30rem;
  margin-bottom: 1.5rem;
  font-size: 1.4rem;
  color: #fff;
  line-height: 1.286;
}
.footer__socials {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.footer__container {
  display: flex;
  align-items: flex-start;
  gap: 16rem;
}
@media (max-width: 768px) {
  .footer__container {
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
  }
}
.footer__nav .nav__list {
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
}
@media (max-width: 768px) {
  .footer__nav .nav__list {
    text-align: center;
    align-items: center;
  }
}
.footer__nav .nav__item {
  color: #fff;
}
.footer__nav .nav__item:nth-child(1) {
  font-weight: 800;
}
.footer__nav .nav__item:nth-child(1) .nav__link {
  font-weight: 800;
}
.footer__nav .nav__link {
  color: #fff;
  font-weight: 400;
}
.footer__btn {
  flex-shrink: 0;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease-out, z-index 0.3s ease-out;
}
.modal.active {
  opacity: 1;
  z-index: 9999;
}
.modal__close {
  position: absolute;
  right: 2rem;
  top: 2rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
.modal__close svg {
  width: 100%;
  height: 100%;
  fill: #000;
}
.modal__body {
  position: relative;
  background-color: #fff;
  padding: 5rem;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}
.modal__left {
  max-width: 40rem;
}
.modal__title {
  display: block;
  font-size: 2.4rem;
  font-weight: 700;
}
.modal__image {
  width: 30rem;
  height: 100%;
}
.modal__image img {
  width: 100%;
  height: 100%;
}

.modal-form__area {
  display: block;
  margin-bottom: 1.2rem;
  width: 100%;
  height: 15rem;
  resize: none;
  overflow-y: auto;
  border: 1px solid #ccc;
  border-radius: 1rem;
  padding: 0.8rem;
  font-size: 1.6rem;
}
.modal-form__area:focus {
  outline: none;
}
.modal-form__input {
  display: block;
  height: 6rem;
  width: 100%;
  margin-bottom: 1.2rem;
  border: 1px solid #ccc;
  border-radius: 1rem;
  padding: 0.8rem;
  font-size: 1.6rem;
  text-align: center;
}
.modal-form__input:focus {
  outline: none;
}
.modal-form__btn {
  font-size: 1.6rem;
}

.menu {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  z-index: 999;
  background-color: #17162c;
  display: none;
  transition: transform 0.3s ease-out;
  transform: translateX(100%);
}
.menu.active {
  transform: translateX(0);
}
.menu__body {
  padding: 5rem 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}
@media (max-width: 1024px) {
  .menu {
    display: block;
  }
}
.menu .footer__col {
  align-items: center;
  text-align: center;
}
.menu .footer__container {
  flex-direction: column;
  gap: 1.5rem;
  align-items: center;
}
.menu .footer__nav .nav__list {
  align-items: center;
  gap: 2rem;
}